@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-7xl: 26px;

  /* Colors */
  --color-white: #fff;
  --color-lavender: rgba(240, 237, 255, 0.8);
  --color-gray: #1c1c1c;
  --color-black: #000;
  --color-gainsboro: #d9d9d9;
  --color-darkslategray: #00404e;

  /* Gaps */
  --gap-5xs: 8px;

  /* Paddings */
  --padding-4xl-9: 23.9px;

  /* border radiuses */
  --br-17xl: 36px;
  --br-base: 16px;
}
